import { Metadata } from 'next';
import { getTranslations } from 'next-intl/server';
import MicrophoneTesterClient from './MicrophoneTesterClient';

export async function generateMetadata({ params }: { params: Promise<{ locale: string }> }): Promise<Metadata> {
    const { locale } = await params;
    const t = await getTranslations({ locale, namespace: 'Tools.microphone-tester.meta' });

    return {
        title: t('title'),
        description: t('description'),
    };
}

export default async function MicrophoneTesterPage({ params }: { params: Promise<{ locale: string }> }) {
    const { locale } = await params;
    return <MicrophoneTesterClient />;
}
